// @ts-nocheck
registerPaint('borderGradient', class {
  static get inputProperties() {
    return [
      '--border-gradient-size',
      '--border-gradient-color1',
      '--border-gradient-color2'
    ]
  }
  paint(ctx, size, props) {
    let color1 = props.get('--border-gradient-color1').toString();
    let color2 = props.get('--border-gradient-color2').toString();
    let border = Number(props.get('--border-gradient-size'));
    let x = border;
    let y = border;
    let width = size.width - (border*2);
    let height = size.height - (border*2);
    let radius = height/2;
    let gradient = ctx.createLinearGradient(0, 0, width, 0);
    gradient.addColorStop('0', color1);
    gradient.addColorStop('1.0', color2);

    ctx.lineWidth = border;
    ctx.strokeStyle = gradient;

    ctx.beginPath();
    ctx.moveTo(x + radius, y);
    ctx.arcTo(x + width, y, x + width, y + height, radius);
    ctx.arcTo(x + width, y + height, x, y + height, radius);
    ctx.arcTo(x, y + height, x, y, radius);
    ctx.arcTo(x, y, x + width, y, radius);
    ctx.closePath();

    ctx.stroke();
  }
});
